.header {
    min-height: 100px;
    margin: 0 0 150px;
    border-bottom: 1px solid #333333;
    float: left;
    width: 100%;
}

.logo-wrapper {
    border: 15px solid #fff;
    float: left;
    position: absolute;
    z-index: 999;
    border-radius: 50%;
    left: 50px;
}

.logo-wrapper .image-wrapper {
    height: 210px;
    width: 210px;
    border: 30px solid purple;
    border-radius: 50%;
    padding: 10px;
    background: #ffffff;
}

.image-wrapper img {
    width: 100px;
    height: 100px;
    z-index: 1;
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.header-logo-section {
    border-bottom: 60px solid purple;
    float: left;
    width: 100%;
    height: 150px;
}

.log-in {
    border-right: 1px solid rgb(166, 166, 166);
    padding: 0 10px;
    margin: 0 15px;
    font-weight: bold;
    cursor: pointer;
    font-size: 22px;
    position: relative;
    top: 5px;
}

.registration-links {
    float: right;
    margin: 20px 0 0;
}

.site-name {
    float: left;
    position: relative;
    left: 300px;
    top: 78px;
    color: #ffffff;
}

.site-name>span {
    font-size: 50px;
    font-weight: bold;
    margin: 0 10px 0 0;
}

.site-name>span+span {
    font-size: 20px;
}

.header-info-container {
    background-color: rgb(242, 230, 242);
    height: 220px;
    width: 100%;
    position: relative;
    top: 150px;
}

.header-info-container>span {
    font-size: 36px;
    position: relative;
    top: -100px;
    left: 370px;
    color: purple;
}

.header-info-container>span.gap-provider {
    left: 450px;
}

@media screen and (max-width: 991px) {
    .header-info-container>span {
        left: 280px;
    }
    .header-info-container>span.gap-provider {
        left: 330px;
    }
}

@media screen and (max-width: 767px) {
    .header {
        margin: 0 0 90px;
    }
    .header-logo-section {
        border-bottom: 40px solid purple;
        height: 95px;
    }
    .logo-wrapper {
        border: 5px solid #fff;
        left: 5px;
        top: 10px;
    }
    .logo-wrapper .image-wrapper {
        height: 120px;
        width: 120px;
        border: 20px solid purple;
        padding: 0;
    }
    .image-wrapper img {
        height: 50px;
        width: 50px;
    }
    .log-in {
        padding: 0 5px;
        margin: 0 5px;
        font-size: 16px;
        top: 2px;
    }
    .header-info-container {
        height: 120px;
        top: 95px;
    }
    .header-info-container>span {
        left: 60px;
        font-size: 18px;
        top: -45px;
    }
    .header-info-container>span.gap-provider {
        left: 110px;
    }
    .registration-links {
        margin: 10px 0 0;
    }
}