.side-nav {
    height: 100%;
    margin:  0 0 40px 0;
}

.nav-tabs .nav-item {
    width: 100%;
    border: 2px solid purple;
    cursor: pointer;
    position: relative;
    width: 50%;
    text-align: center;
    font-size: 22px;
}

.nav-tabs .nav-item.active::after {
    content: '';
    position: absolute;
    top: 50px;
    right: calc(50% - 10px);
    font-weight: bold;
    width: 0; 
    height: 0; 
    border-left: 20px solid transparent;
    border-right: 20px solid transparent;
    border-top: 20px solid purple;;
}

.nav-tabs .nav-item.active {
    background-color: purple;
    color: #ffffff;
    border-color: purple;
}

.nav-tabs .nav-item.active a:not([href]):not([tabindex]) {
    color: #ffffff;
}

.nav-tabs .nav-item.active::after {
    color: #ffffff;
}

.nav-tabs .nav-link:focus,
.nav-tabs .nav-link:hover,
.nav-tabs .nav-link:visited,
.nav-tabs .nav-link:active {
    border-color: transparent;
}


@media screen and (max-width: 767px) {
    .nav-tabs .nav-item {
        font-size: 18px;
    }
    .nav-tabs .nav-item.active::after {
        top: 40px;
    }
}