body {
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif
}

h1, h2, h3, h4 {
    color: purple;
}

.btn-primary,
.btn-primary:not(:disabled):not(.disabled):active,
.btn-primary:not(:disabled):not(.disabled).active,
.btn-primary:not(:disabled):not(.disabled):hover,
.btn-primary:not(:disabled):not(.disabled):focus,
.btn-primary:not(:disabled):not(.disabled):visited,
.show>.btn-primary.dropdown-toggle {
    color: #fff;
    background-color: purple;
    border-color: purple;
    width: 200px;
    height: 50px;
    font-size: 22px;
}

.btn-secondary,
.btn-secondary:not(:disabled):not(.disabled):active,
.btn-secondary:not(:disabled):not(.disabled).active,
.btn-secondary:not(:disabled):not(.disabled):hover,
.btn-secondary:not(:disabled):not(.disabled):focus,
.btn-secondary:not(:disabled):not(.disabled):visited,
.show>.btn-secondary.dropdown-toggle {
    color: purple;
    background-color: #ffffff;
    border: 2px solid purple;
    width: 200px;
    height: 50px;
    font-size: 22px;
}

.btn-primary:not(:disabled):not(.disabled):active:focus,
.btn-primary:not(:disabled):not(.disabled):active:hover,
.btn-primary:not(:disabled):not(.disabled):active:visited,
.btn-primary:not(:disabled):not(.disabled).active:focus,
.show>.btn-primary.dropdown-toggle:focus,
.btn-secondary:not(:disabled):not(.disabled):active:focus,
.btn-secondary:not(:disabled):not(.disabled):active:hover,
.btn-secondary:not(:disabled):not(.disabled):active:visited,
.btn-secondary:not(:disabled):not(.disabled).active:focus,
.show>.btn-secondary.dropdown-toggle:focus {
    box-shadow: none;
}

a:not([href]):not([tabindex]) {
    color: purple;
}

label {
    color: purple;
    font-size: 18px;
}

.form-control {
    border: 2px solid purple;
}

@media screen and (max-width: 992px) {
    .container.app-body {
        max-width: 950px;
    }
}

@media screen and (max-width: 767px) {
    .container.app-body {
        max-width: 720px;
    }
    .btn-primary,
    .btn-primary:not(:disabled):not(.disabled):active,
    .btn-primary:not(:disabled):not(.disabled).active,
    .btn-primary:not(:disabled):not(.disabled):hover,
    .btn-primary:not(:disabled):not(.disabled):focus,
    .btn-primary:not(:disabled):not(.disabled):visited,
    .show>.btn-primary.dropdown-toggle {
        width: 90px;
        height: 35px;
        font-size: 16px;
        line-height: 120%;
    }
}

.btn + .btn {
    margin: 0 10px;
}

@media screen and (max-width: 576px) {
    .container-fluid {
        padding: 0;
    }
    .container.app-body {
        max-width: 540px;
        ;
    }
}


.side-nav {
    height: 100%;
    margin:  0 0 40px 0;
}

.nav-tabs .nav-item {
    width: 100%;
    border: 2px solid purple;
    cursor: pointer;
    position: relative;
    width: 50%;
    text-align: center;
    font-size: 22px;
}

.nav-tabs .nav-item.active::after {
    content: '';
    position: absolute;
    top: 50px;
    right: calc(50% - 10px);
    font-weight: bold;
    width: 0; 
    height: 0; 
    border-left: 20px solid transparent;
    border-right: 20px solid transparent;
    border-top: 20px solid purple;;
}

.nav-tabs .nav-item.active {
    background-color: purple;
    color: #ffffff;
    border-color: purple;
}

.nav-tabs .nav-item.active a:not([href]):not([tabindex]) {
    color: #ffffff;
}

.nav-tabs .nav-item.active::after {
    color: #ffffff;
}

.nav-tabs .nav-link:focus,
.nav-tabs .nav-link:hover,
.nav-tabs .nav-link:visited,
.nav-tabs .nav-link:active {
    border-color: transparent;
}


@media screen and (max-width: 767px) {
    .nav-tabs .nav-item {
        font-size: 18px;
    }
    .nav-tabs .nav-item.active::after {
        top: 40px;
    }
}
.RichEditor-root {
    background: #fff;
    border: 2px solid purple;
    font-family: 'Georgia', serif;
    font-size: 14px;
    padding: 15px;
    margin: 0 0 30px;
  }
  
  .RichEditor-editor {
    border-top: 1px solid purple;
    cursor: text;
    font-size: 16px;
    margin-top: 10px;
  }
  
  .RichEditor-editor .public-DraftEditorPlaceholder-root,
  .RichEditor-editor .public-DraftEditor-content {
    margin: 0 -15px -15px;
    padding: 15px;
  }
  
  .RichEditor-editor .public-DraftEditor-content {
    min-height: 200px;
  }
  
  .RichEditor-hidePlaceholder .public-DraftEditorPlaceholder-root {
    display: none;
  }
  
  .RichEditor-editor .RichEditor-blockquote {
    border-left: 5px solid #eee;
    color: #666;
    font-family: 'Hoefler Text', 'Georgia', serif;
    font-style: italic;
    margin: 16px 0;
    padding: 10px 20px;
  }
  
  .RichEditor-editor .public-DraftStyleDefault-pre {
    background-color: rgba(0, 0, 0, 0.05);
    font-family: 'Inconsolata', 'Menlo', 'Consolas', monospace;
    font-size: 16px;
    padding: 20px;
  }
  
  .RichEditor-controls {
    font-family: 'Helvetica', sans-serif;
    font-size: 14px;
    margin-bottom: 5px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
  }
  
  .RichEditor-styleButton {
    color: #999;
    cursor: pointer;
    margin-right: 16px;
    padding: 2px 0;
    display: inline-block;
  }
  
  .RichEditor-activeButton {
    color: #5890ff;
  }

  button + button {
    margin: 0 10px 0 0;
  }

  @media screen and (max-width: 767px) {
    .RichEditor-root {
        margin: 10px 0;
    }
}
.story-block {
    text-align: center;
    padding: 0;
    margin: 0 0 75px 0;
}

.story-block h4 {
    text-align: center;
    text-decoration: underline;
}

.story-block .story-lines {
    text-align: left;
    max-height: 240px;
    overflow: hidden;
}

.story-block .read-more-button {
    margin: 10px 0 0;
}

.story-image-container {
    margin: 0 10px 0 0;
    float: left;
}

.story-image-container img {
    height: 140px;
    width: 140px;
}

.user-story-container {
    position: relative;
    top: 50px;
    border: 2px solid purple;
    padding: 15px;
}

.user-image {
    width: 100px;
    height: 100px;
    border: 2px solid purple;
    border-radius: 50%;
    position: relative;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    top: -65px;
}

.story-description {
    margin: -50px 0 0;
}

.user-image img {
    height: 96px;
    width: 96px;
    border-radius: 50%;
}

@media screen and (max-width: 767px) {
    .story-block {
        margin: 15px 0 70px;
    }
    .story-block h4 {
        margin: 0 0 20px 0;
        font-size: 20px;
    }
}

.header {
    min-height: 100px;
    margin: 0 0 150px;
    border-bottom: 1px solid #333333;
    float: left;
    width: 100%;
}

.logo-wrapper {
    border: 15px solid #fff;
    float: left;
    position: absolute;
    z-index: 999;
    border-radius: 50%;
    left: 50px;
}

.logo-wrapper .image-wrapper {
    height: 210px;
    width: 210px;
    border: 30px solid purple;
    border-radius: 50%;
    padding: 10px;
    background: #ffffff;
}

.image-wrapper img {
    width: 100px;
    height: 100px;
    z-index: 1;
    position: relative;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.header-logo-section {
    border-bottom: 60px solid purple;
    float: left;
    width: 100%;
    height: 150px;
}

.log-in {
    border-right: 1px solid rgb(166, 166, 166);
    padding: 0 10px;
    margin: 0 15px;
    font-weight: bold;
    cursor: pointer;
    font-size: 22px;
    position: relative;
    top: 5px;
}

.registration-links {
    float: right;
    margin: 20px 0 0;
}

.site-name {
    float: left;
    position: relative;
    left: 300px;
    top: 78px;
    color: #ffffff;
}

.site-name>span {
    font-size: 50px;
    font-weight: bold;
    margin: 0 10px 0 0;
}

.site-name>span+span {
    font-size: 20px;
}

.header-info-container {
    background-color: rgb(242, 230, 242);
    height: 220px;
    width: 100%;
    position: relative;
    top: 150px;
}

.header-info-container>span {
    font-size: 36px;
    position: relative;
    top: -100px;
    left: 370px;
    color: purple;
}

.header-info-container>span.gap-provider {
    left: 450px;
}

@media screen and (max-width: 991px) {
    .header-info-container>span {
        left: 280px;
    }
    .header-info-container>span.gap-provider {
        left: 330px;
    }
}

@media screen and (max-width: 767px) {
    .header {
        margin: 0 0 90px;
    }
    .header-logo-section {
        border-bottom: 40px solid purple;
        height: 95px;
    }
    .logo-wrapper {
        border: 5px solid #fff;
        left: 5px;
        top: 10px;
    }
    .logo-wrapper .image-wrapper {
        height: 120px;
        width: 120px;
        border: 20px solid purple;
        padding: 0;
    }
    .image-wrapper img {
        height: 50px;
        width: 50px;
    }
    .log-in {
        padding: 0 5px;
        margin: 0 5px;
        font-size: 16px;
        top: 2px;
    }
    .header-info-container {
        height: 120px;
        top: 95px;
    }
    .header-info-container>span {
        left: 60px;
        font-size: 18px;
        top: -45px;
    }
    .header-info-container>span.gap-provider {
        left: 110px;
    }
    .registration-links {
        margin: 10px 0 0;
    }
}
